.rating-css {
  height: 100px;
  width: 1000px;
  padding-left:100px;
}
.rating-css  .star-icon{
  color: #e8f539;
  font-size: 30px;
  font-family: sans-serif;
  font-weight: 800;
  text-align: center;
  text-transform: uppercase;
  padding-top: 10px;
  padding-left:300px;
}

.rate{
  font-size: 1.5rem;
  font-weight: bold;
  font-family: sans-serif;
  padding-left: 300px;
  font-family: sans-serif;
  
}

.rating-css input {
  display: none;
}
.rating-css input + label {
  font-size: 60px;
  text-shadow: 1px 1px 0 #e8f539;
  cursor: pointer;
  padding-right: 2%;
}
.rating-css input:checked + label ~ label {
  color: #838383;
}
.rating-css label:active {
  transform: scale(0.8);
  transition: 0.3s ease;
}

