.navbar {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    white-space: nowrap;
  }

  .fa-staylinked{
    font-size: 1.8rem;

  }
  .fa-caret-down{
    margin-left: 10px;
  }
  .fa-home{
    font-size: 1rem;
    margin-right: 5px;
  }
  .fa-address-card{
    font-size: 1rem;
    margin-right: 5px;
  }
  .fa-window-restore{
    font-size: 1rem;
    margin-right: 5px;
  }
  .fa-question{
    font-size: 1rem;
    margin-right: 5px;
  }

  .fa-user-edit{
    font-size: 1rem;
    margin-right: 5px;
  }
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 5px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content:center;

    margin-right: 2rem;
  }

  .nav-item {
    display: flex;
    align-items: center;
    height: 80px;
  }

  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }

  .nav-links:hover {
    /* border-bottom: 6px solid #fff; */
    border-bottom:2px solid #fff;
    transition: all 0.2s ease-out;
  }

  .fa-bars {
    color: rgb(255, 255, 255);
  }

  .nav-links-mobile {
    display: none;
  }

  .menu-icon {
    display: none;
  }
  .btn1{
    justify-content: space-between;
  }
  .searchContainer {
    display: inline-flex;
    flex: 1 1 400px;
    position: relative;
    border: 1px solid #ccc;
    border-radius: 5px;
    overflow: hidden;
    margin-right:20px;
  }

  .search{
    width:200px;
  }

  .search-btn{
    padding: 0.5rem 0.5rem 0.5rem 0;
    background:black;
    border: 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: white;
    border-radius: 0;
    width: 30px;

    text-align: center;
  }

  .fa-search{
    margin-left:5px;
  }
  @media screen and (max-width: 960px) {
    .search-btn{
      display: all;
      width:200px;
    }
    .searchContainer{
      display:all;
      margin-top:10px;
      margin-right:90px;
      margin-left:380px;
      width:220px;
      height:40px;
    }
    .btn1{
      display: none;
    }
    .fa-caret-down{
      display: none;
    }
    .NavbarItems {
      position: relative;
    }

    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;

      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }

    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }

    .nav-links {
      text-align: center;
      padding-top: 25px;
      width: 100%;
      display: table;
    }

    .nav-links:hover {
      background: #fff;
      color: #242424;
      border-radius: 0;
    }

    .navbar-logo {
      position: absolute;
      top: 0;
      font-size:35px;
      left:0;
      transform: translate(5%, 50%);
    }

    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }

    .fa-times {
      color: #fff;
      font-size: 1.5rem;
    }

    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 0.5rem auto;
      border-radius: 4px;
      width: 70%;
      text-decoration: none;
      font-size: 1rem;
      background-color: transparent;
      color: #fff;
      padding: 1rem;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }

    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }